import { Case } from './case';

export class Grille {

    nbCasesVides: number;
    cases = [];
    tour : number ;

    constructor() {

        this.initGame();
        
    }

    initGame() {

        this.nbCasesVides = 9;  
        // initialisation des cases
        this.cases = [];

        this.tour = 0;  
        
        for (let i = 0; i < this.nbCasesVides; i++) {
        const oCase = new Case();
        oCase.setValue('');
        oCase.setcroixVisible('none');
        oCase.setrondVisible('none');
        this.cases.push(oCase);
        }

    }

}
