import { Component, OnInit } from '@angular/core';
import { Grille } from '../../Core/grille'
import { GameService } from '../../Core/game-service';
import {Howl, Howler} from 'howler';
import {ContactFormComponent} from '../contact-form/contact-form.component';

@Component({
  selector: 'grille-jeu',
  templateUrl: './grille-jeu.component.html',
  styleUrls: ['./grille-jeu.component.scss']
})
export class GrilleJeuComponent implements OnInit {

  cases = [];
  tour : number ;
  gameOn : boolean;
  sound : any;
  soundtoggle : boolean;
  playerText : string;

  constructor( private grille: Grille , private gameService : GameService, private contact : ContactFormComponent) {
    this.tour = gameService.tour ;
    this.gameOn = false;
    this.cases = grille.cases;
    this.sound = new Howl({
      src: ['assets/sounds/click.mp3'],
      onplay: function() {
        console.log('PLAYING ... ');
    }});
    this.soundtoggle = false;
    this.playerText = '';
  }

  onclicked(agreed: boolean) {
    this.playGame();
  }

  onclickedplayagain(agreed: boolean) {
    this.playAgain();
  }

  playerClick(i: number) {
    this.sound.stop();
    if (this.grille.tour === 0) {
      this.play(i);
    }
    this.sound.play();
  }

  play(i: number) :void {
    if (this.grille.cases[i].value === '')
    {
      if (this.tour === 0) {
        this.grille.cases[i].setValue('C');
        this.grille.cases[i].setcroixVisible('block');
        this.grille.cases[i].setrondVisible('none');
      } else {
        this.grille.cases[i].setValue('R');
        this.grille.cases[i].setcroixVisible('none');
        this.grille.cases[i].setrondVisible('block');
      }
      this.tour = this.gameService.changementDeJoueur();
      this.changePlayerText();
    }
  }

  playGame() {
    this.grille.initGame();
    this.cases = this.grille.cases;
    this.tour = 0;
    this.gameOn = true;
    this.changePlayerText();
  }

  playAgain() {
    this.grille.initGame();
    this.cases = this.grille.cases;
    this.gameService.tour = 0;
    this.tour = 0;
    this.changePlayerText()
  }

  getVisibility(i: number, s : string) {
    console.log(' case ' + i + ' s'  +s + ' val ' + this.grille.cases[i].value + 'tour' + this.tour);
    switch (s) {
      case '1':
        if (this.tour === 0) {
          if (this.grille.cases[i].value ==='C' || this.grille.cases[i].value ==='' ) {
            return 'none';
          } else {
            return 'block';
          }
        }
        return 'none';
        break;
      case '2':
          if (this.tour === 1) {
            if (this.grille.cases[i].value ==='R') {
              return 'block';
            } else {
              return 'none';
            }
          }
          return 'none';
          break;
      default:
        return 'none';
        break;
    }
  }

  toggleSound()  {
    if (this.soundtoggle === false) {
      this.soundtoggle = true;
    } else {
      this.soundtoggle = false;
    }
  }

  changePlayerText() {
    if (this.tour === 0) {
      this.playerText = 'Ready player one plays';
    } else {
      this.playerText = 'Ready player two plays';
    }
  }

  ngOnInit() {
  }

}
