export class Player {
    name : string;
    id   : string;
    visible : string;

    constructor(name : string, id : string,  visible:string ) {
        this.name = name;
        this.id = id;
        this.visible = visible;
    }
    
    setVisible() {
        this.visible = 'block';
    }
    
    setNoneVisible() {
        this.visible = 'none';
    }

    getVisible() {
        return this.visible;
    }
    
}
