import { Component, OnInit, EventEmitter, Input, Output} from '@angular/core';
//import {GrilleJeuComponent} from '../../app/grille-jeu/grille-jeu.component';
import {Player} from '../../Core/player';
import {Howl, Howler} from 'howler';

declare var $: any;

@Component({
  selector: 'contact-form',
  templateUrl: './contact-form.component.html',
  styleUrls: ['./contact-form.component.scss']
})

export class ContactFormComponent implements OnInit {

  players = [];
  sound1 : any;
  sound2 : any;
  player1play : boolean;
  player2play : boolean;
  playDisable : boolean;

  @Output() onclicked = new EventEmitter<boolean>();
  @Output() onclickedplayagain = new EventEmitter<boolean>();
  
  //constructor(private grilleJeuComponent:GrilleJeuComponent) { 
  constructor() { 
    const player1 = new Player("Ready player one","player1","block");
    this.players.push(player1);
    const player2 = new Player("Ready player two","player2","block");
    this.players.push(player2);
    this.sound1 = new Howl({
      src: ['assets/sounds/counter.mp3'],
      onplay: function() {
      },
      onend: function() {
      }
    });
    
    this.sound2 = new Howl({
      src: ['assets/sounds/Xenon2.mp3'],
      onplay: function() {
      },
      onend: function() {
      }
    });
    this.playDisable = false;
  }
  
  log() { 
  }
  
  playGame() {
    this.sound1.stop();
    this.sound2.stop();
    this.sound1.play();
    setTimeout( () => { this.sound2.play();  }, 2000 );
    $("#overlay").modal('show');
    setTimeout(function() {
      $('#overlay').modal('hide');
    }, 2000);
    this.onclicked.emit(true);
    this.players[0].setVisible();
    this.players[1].setNoneVisible();
    this.playDisable = true;
  }
  
  playAgain() {
    this.onclickedplayagain.emit(true);
    this.players[0].setVisible();
    this.players[1].setNoneVisible();
  }
  
  restart() {
    this.onclickedplayagain.emit(true);
    this.players[0].setVisible();
    this.players[1].setVisible();
  }

  stopSound(toggle : boolean) {
    this.sound1.mute(toggle);
    this.sound2.mute(toggle);
    console.log(toggle);
  }
  
  setVisible(tour : number){
    if (tour == 0)
    {
      this.players[0].setVisible();
      this.players[1].setNoneVisible();
    }
    else
    {
      this.players[1].setVisible();
      this.players[0].setNoneVisible();
    }
  }

  ngOnInit() {
  }

}
